.app-wrapper {
    margin: 0 auto;
    padding: 7px;
    width: 1200px;
    display: grid;
    grid-template-areas:
    "h h"
    "n c";
    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    /*grid-gap: 10px;*/

}

.app-wrapper-content {
    grid-area: c;
    background-color: #d4d8dc;
}



